import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';


@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {

  your_name: string="";
  gender: string="";
  date_birth: string="";
  email_address: string="";
  password: string="";
  confirm_pass: string="";

  disabledButton;


  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders

  ) { }

  ngOnInit() {
  }

  ionViewDiEnter(){
    this.disabledButton = false;
  }
  

  async tryRegister(){
    if(this.your_name==""){
      this.presentToast('Tu nombre es requerido');
    }else if(this.gender==""){
      this.presentToast('El genero es requerico');
    } else if(this.date_birth==""){
      this.presentToast('Se requiere la Fecha de Nacimiento');
    } else if(this.email_address==""){
      this.presentToast('Se requiere el usuario');
    } else if(this.password==""){
      this.presentToast('Se requiere la Contraseña');
    } else if(this.confirm_pass!= this.password){
      this.presentToast('Se requiere que confirme su Contraseña');
    }else{
      this.disabledButton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'proses_register',
          your_name: this.your_name,
          gender: this.gender,
          date_birth: this.date_birth,
          email_address: this.email_address,
          password: this.password
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledButton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/login']);
          }else{
            loader.dismiss();
            this.disabledButton = false;
            this.presentToast(res.msg);
          }
        },(err)=>{
          loader.dismiss();
          //this.presentToast('registro ');
          //this.router.navigate(['/login']);
          this.disabledButton = false;
          this.presentAlert('Timeout');

        });
        
      });
    }
  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }

  async presentAlert(a) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
            //action
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.tryRegister();
          }
        }
      ]
    });

    await alert.present();
  }

}
